 .pb-24 {
	padding-bottom: 6rem;
  }
  .px-4 {
	padding-left: 1rem;
	padding-right: 1rem;
  }
  .pb-4 {
	padding-bottom: 1rem;
  }
  .pt-10 {
	padding-top: 2.5rem;
  }
  .container {
	margin-left: auto;
	margin-right: auto;
	padding-left: 1rem;
	padding-right: 1rem;
	width: 100%;
  }
  .w-full {
	width: 100%;
  }
  .-mx-4 {
	margin-left: -1rem;
	margin-right: -1rem;
  }
  .flex {
	display: flex;
  }
  .items-center {
	align-items: center;
  }
  .flex-wrap {
	flex-wrap: wrap;
  }
  .w-6\/12 {
	width: 50%;
  }
  .max-w-\[622px\] {
	max-width: 622px;
  }
  .h2 {
	font-size: 42px;
	line-height: 1.2;
  }

.ban_sec{
	max-width: 100% !important;
	width: 100%;
}
  .h2 strong {
	-webkit-background-clip: text;
	background-clip: text;
	background-image: linear-gradient(134.03deg,#e73671 10.3%,#8f3aa6 80.97%);
	color: transparent;
	font-weight: 700;
  }
  .text-black-100 {
	--tw-text-opacity: 1;
	color: rgb(1 1 1/var(--tw-text-opacity));
  }
  .text-base {
	font-size: 16px;
	line-height: 28px;
  }
  .\[\&\>a\]\:btn a,
  .\[\&\>a\]\:btn button {
	border-radius: 8px;
	display: inline-block;
	--tw-bg-opacity: 1;
	background-color: rgb(231 54 113/var(--tw-bg-opacity));
	font-size: 16px;
	font-weight: 600;
	line-height: 28px;
	padding: 1rem 2.5rem;
	--tw-text-opacity: 1;
	color: rgb(255 255 255/var(--tw-text-opacity));
	transition-duration: .3s;
	transition-property: all;
	transition-timing-function: cubic-bezier(.4,0,.2,1);
	border: none;
	text-decoration: none;
}
.\[\&\>a\]\:btn a:hover,
.\[\&\>a\]\:btn button:hover {
	--tw-bg-opacity: 1;
	background-color: rgb(143 58 166/var(--tw-bg-opacity));
}
img, video {
	height: auto;
	max-width: 100%;
  }
  .mx-auto {
	margin-left: auto;
	margin-right: auto;
  }
  @media (min-width: 1532px) {
	.container {
	  max-width: 1532px;
	}
  }
  @media (min-width: 1272px) {
	.container {
	  max-width: 1272px;
	}
  }